<template>
  <div id="searchBar" :style="searchBarCss">
    <form @submit.prevent="formSubmit" action="javascript:return true">
      <van-image class="searchIcon" :src='searchIcon_src'/>
      <input type="search" ref="input" @keydown.13="search" @input="searchInput" @focusout="searchInputFocusout = true" placeholder="搜索名称、颜色" v-model="searchValue"/>
      <van-image class="deleteIcon" :src='deleteIcon_src' @click="deleteSearchValue" v-if="searchValue"/>
    </form>
  </div>
</template>

<script>
export default {
  data () {
    return {
      searchBarCss: null,
      searchValue: null,
      searchInputFocusout: false,
      searchIcon_src: require('@/assets/search.png'),
      deleteIcon_src: require('@/assets/err.png')
    }
  },
  mounted () {
    this.searchBarAnimation()
  },
  methods: {
    search () {
      const value = this.searchValue
      if (value === null || value === '') return
      this.$emit('searchValue', this.searchValue)
      document.activeElement.blur()
    },
    searchInput () {
      this.searchValue = this.searchValue.replace(/[^\a-\z\A-\Z0-9\u4E00-\u9FA5]/g, '')
      if (this.searchValue === '') this.$emit('deleteSearchValue')
    },
    deleteSearchValue () {
      this.searchValue = null
      if (this.searchInputFocusout) {
        this.$nextTick(() => {
          this.$refs.input.focus()
        })
      }
      this.$emit('deleteSearchValue')
    },
    formSubmit () {
      return false
    },
    searchBarAnimation () {
      setTimeout(() => {
        this.searchBarCss = 'transform: scale(1);opacity: 1;'
      }, 0)
    }
  }
}
</script>

<style lang="scss" scoped>
#searchBar{
  width: 90%;
  height: 50px;
  background: #ffffff;
  border: 4px solid #000000;
  box-sizing: border-box;
  border-radius: 20px;
  overflow: hidden;
  display: flex;
  justify-content: center;
  transition: all .5s;
  transform: scale(.1);
  opacity: 0;
  form{
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    .searchIcon{
      width: 30px;
      margin-left: 13px;
    }
    input{
      width: 100%;
      height: 100%;
      outline: none;
      appearance: none;
      -webkit-appearance: none;
      border: 0;
      padding: 0px 13px;
      background: #ffffff;
    }
    .deleteIcon{
      width: 30px;
      margin-right: 13px;
    }
  }
}
</style>
